import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Data, Params, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { SortDescriptor } from '@progress/kendo-data-query';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { ToastrService } from 'ngx-toastr';
import { Subscription, combineLatest } from 'rxjs';
import {
  ColumnDisplayType,
  Contact,
  ContactList,
  FieldDefinition,
  ICombinedColumn,
  MetaDataType,
  TableItem,
} from 'shared-code-lib';
import { ErrorsService } from '../../../blocks/services/errors.service';
import { InterFAXService } from '../../../blocks/services/interfax.service';
import { ApiResults, Control } from '../../../blocks/services/models';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';

@Component({
  selector: 'app-contact-list-properties',
  templateUrl: './contact-list-properties.component.html',
  styleUrls: ['./contact-list-properties.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ContactListPropertiesComponent implements OnInit, OnDestroy {
  formControls: Array<Control> = [];

  saveButton: Control;
  cancelButton: Control;

  routeParams: Params;
  routeData: Data;

  fieldName: FieldDefinition;

  contactList: ContactList;

  show: boolean;

  items: TableItem[] = [];
  columns: ICombinedColumn[];
  sort: SortDescriptor;

  contact: Contact;
  contactIndex = -1;
  addContactIndex = -1;

  title = '';

  subscriptions = new Subscription();

  constructor(
    private translateService: TranslateService,
    private router: Router,
    private route: ActivatedRoute,
    private interfaxService: InterFAXService,
    private toastrService: ToastrService,
    private errorsService: ErrorsService,
    private spinnerService: SpinnerStateService,
    private ngxSmartModalService: NgxSmartModalService
  ) {}

  ngOnInit(): void {
    this.createButtons();
    this.createFields();

    this.sort = { field: '1', dir: 'asc' };

    this.columns = this.getColumns();

    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        this.routeParams = params;
        this.routeData = data;
        this.load();
      })
    );
  }

  onAdd = (): void => {
    this.contact = new Contact();
    this.contact.id = this.addContactIndex--;
    this.contactIndex = -1;

    this.openContactModal(this.contact);
  };

  onDelete = (item: Contact): void => {
    this.contactList.members = this.contactList.members.filter((c) => c.id != item.id);
    this.items = this.contactList.members.map((e) => this.createTableItem(e));
  };

  onProperties = (item: Contact): void => {
    this.contact = new Contact(item);
    this.contactIndex = this.contactList.members.indexOf(item);
    this.openContactModal(this.contact);
  };

  openContactModal = (c: Contact): void => {
    this.ngxSmartModalService.setModalData(
      { skipSaving: true, contact: new Contact(c), onSave: this.onSaveContact },
      'interfaxContactModal',
      true
    );
    this.ngxSmartModalService.open('interfaxContactModal');
  };

  onSaveContact = (item: Contact): void => {
    this.contact = item;
    if (this.contactIndex == -1) {
      this.contactList.members.push(new Contact(this.contact));
    } else {
      this.contactList.members[this.contactIndex] = new Contact(this.contact);
    }

    this.items = this.contactList.members.map((e) => this.createTableItem(e));
    this.closeContactModal();
  };

  closeContactModal = (): void => {
    this.ngxSmartModalService.close('interfaxContactModal');
    this.ngxSmartModalService.resetModalData('interfaxContactModal');
  };

  onSave = () => {
    this.spinnerService.showSavingSpinner();
    this.interfaxService.setContactList(this.contactList, this.saved, this.saveFailed);
  };

  saved = (results: ApiResults) => {
    this.spinnerService.hide();

    if (results.success) {
      this.toastrService.success(this.translateService.instant('contact-list.save-success'));
      this.close();
    } else {
      this.errorsService.showErrorsModal(results.errors, 'contact-list.save-fail');
    }
  };

  saveFailed = (): void => {
    this.spinnerService.hide();
    this.toastrService.error(this.translateService.instant('contact-list.save-fail'));
  };

  onCancel = () => {
    this.close();
  };

  close = () => {
    this.router.navigateByUrl(
      'product/settings/InterFAX/InterFAX-Upland.Bamba.InterFAX.Administration.Contacts.Folder'
    );
  };

  private load = () => {
    if (this.routeData.type === 'properties') {
      this.title = this.translateService.instant('contact-list.title');

      this.spinnerService.show(this.translateService.instant('Common.LoadingData'), document.documentElement);
      this.interfaxService.getContactList(this.routeParams.contactListId, this.loaded, this.loadFailed);
      return;
    }

    this.title = this.translateService.instant('contact-list.title-add-new');

    this.contactList = new ContactList();
    this.items = [];
    this.show = true;
  };

  private loaded = (cl: ContactList) => {
    this.spinnerService.hide();
    this.contactList = cl;
    this.items = cl.members.map((m) => this.createTableItem(m));
    this.show = true;
  };

  private loadFailed = () => {
    this.spinnerService.hide();
  };

  private createButtons() {
    this.saveButton = new Control(this.onSave, 'Save', 'btn btn-primary btn-minwidth', '', '', 'btn', false);
    this.cancelButton = new Control(this.onCancel, 'Cancel', 'btn btn-light btn-minwidth', '', '', 'btn', false);
    this.formControls.push(this.saveButton);
    this.formControls.push(this.cancelButton);

    this.translateService.get('Metadata.Save').subscribe((res) => {
      this.saveButton.setTranslation(res);
    });
    this.translateService.get('Metadata.Cancel').subscribe((res) => {
      this.cancelButton.setTranslation(res);
    });
  }

  private createFields = () => {
    this.fieldName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-role.name'),
      placeholder: this.translateService.instant('admin-role.name'),
      required: true,
    });
  };

  createTableItem(c: Contact): TableItem {
    (<any>c).icon = 'fa-address-card'; //don't do this at home folks

    const tableItem = new TableItem(c, this.columns, this.translateService);

    tableItem.data.defaultAction = {
      name: this.translateService.instant('admin-outbound-fax-settings.properties'),
    };

    return tableItem;
  }

  getColumns(): Array<ICombinedColumn> {
    const columns: Array<ICombinedColumn> = [];

    columns.push(this.createColumn('icon', ColumnDisplayType.FontAwesome, 32, false));
    columns.push(this.createColumn('name', ColumnDisplayType.ActionLink, 200, true));
    columns.push(this.createColumn('faxNumber', ColumnDisplayType.Text, 200, false));
    columns.push(this.createColumn('company', ColumnDisplayType.Text, 200, false));
    columns.push(this.createColumn('phoneNumber', ColumnDisplayType.Text, 200, false));

    return columns;
  }

  createColumn(field: string, displayType: number, width: number, stretch: boolean) {
    return {
      id: 'column_' + field,
      width: width,
      stretch: stretch,
      displayType: displayType,
      headerImage: '',
      headerText: `contact-list.${field}`,
      itemProperty: field,
      columnName: field,
      sortable: false,
      sortByFieldName: '',
      invertBooleanValue: false,
    };
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
